import React from 'react';

import classNames from 'classnames';

import { isEmpty, omitBy, isUndefined, trim } from 'lodash';
import { useBlockProps } from '@wordpress/block-editor';

function save ( props ) {
	let styles = {
		'--ghub-afq-color': props.attributes.textColor,
		'--ghub-afq-bg-color': props.attributes.background,
		'--ghub-afq-gradient-color': props.attributes.gradientBackground,
	};

	const justificationClass =
		"justify-content-" + props.attributes?.layout?.justifyContent ?? "left";
	const blockProps = useBlockProps.save( {
		className: classNames( 'gutenberghub-query-result-count ' + justificationClass ),
		style: omitBy( styles, ( value ) =>
			isUndefined( value ) || value === false || trim( value ) === '' )
	} );

	// Leaving sprintf argument to easily replace on server-side.
	return <div { ...blockProps }><p>%1$s</p></div>;
}

export default save;
