import React from 'react';
import {__} from "@wordpress/i18n"
import { RichText, useBlockProps,InspectorControls } from '@wordpress/block-editor';
import { Tip, PanelBody } from "@wordpress/components"
import { isEmpty, omitBy, isUndefined, trim } from 'lodash';
import classNames from 'classnames';
import { CustomToggleGroupControl } from "../../components"
						


function edit ( props ) {

	let styles = {
		'--ghub-afq-color': props.attributes.textColor,
		'--ghub-afq-bg-color': props.attributes.background,
		'--ghub-afq-gradient-color': props.attributes.gradientBackground,
	};
	const justificationClass =
		"justify-content-" + props.attributes?.layout?.justifyContent ?? "left";

	const blockProps = useBlockProps( {
		className: classNames( 'gutenberghub-query-result-count ' + justificationClass ),
		style: omitBy( styles, ( value ) =>
			isUndefined( value ) || value === false || trim( value ) === '' )
	} );
	const hide = props.attributes.hide
	return (
		<div { ...blockProps }>
			<p >
				<RichText
					placeholder={__("Prefix","ghub-query-result-count")}
					tagName="span"
					value={ props.attributes.startText }
					onChange={ ( newValue ) => {
						props.setAttributes( { startText: newValue } );
					} }
				/>
				{hide !== 'result-count' && <span> X </span>}
				<RichText
					placeholder={__("Mid","ghub-query-result-count")}
					tagName="span"
					value={ props.attributes.centerText }
					onChange={ ( newValue ) => {
						props.setAttributes( { centerText: newValue } );
					} }
				/>
				{hide !== 'total-count' && <span> XX </span>}
				<RichText
					placeholder={__("Suffix","ghub-query-result-count")}
					tagName="span"
					value={ props.attributes.endText }
					onChange={ ( newValue ) => {
						props.setAttributes( { endText: newValue } );
					} }
				/>
			</p>
			<InspectorControls>
				<div style={{ padding:"20px 0" }}>
					<PanelBody title={__("General","ghub-query-result-count")}>						 
						  <CustomToggleGroupControl 
						  	isBlock
							attributeKey="hide"
						  	label={__("Hide Count","ghub-query-result-count")}
							options={[
								{
									label:__("None","ghub-query-result-count"),
									value:"none"
								},
								{
									label:__("Result","ghub-query-result-count"),
									value:"result-count"
								},
								{
									label:__("Total","ghub-query-result-count"),
									value:"total-count"
								}
							]}
						  />
						<Tip >
							{__("Actual result count will display on frontend only.","ghub-query-result-count")}
						</Tip>
					</PanelBody>
				</div>
			</InspectorControls>
		</div>
	);
}

export default edit;
